<template>
  <div class="hello">
    <div style="position:fixed:top:0px;z-index:1">
      <!-- title -->
      <cube-button :active="true">基本信息</cube-button>
    </div>
    <div style="margin-top:50px;">
      <yInput :fields='fields' :model='model' @input="setInput"></yInput>
    </div>
  </div>
</template>

<script>
import yInput from './public/yInput'
export default {
  name: 'hello',
  data () {
    return {
      validity: {},
      valid: undefined,
      model: {
        inputValue:'',
        email:'',
        tel:'',
        date:'',
        age:'26',
        selectValue:{ text: '是', value: 'aa'},
        isFlag:''
      },//Submit Data;
      fields: [
        {
          type: 'input',
          modelKey: 'inputValue',
          label: '姓名',
          props: {
            placeholder: '请输入'
          },
          rules: {
            required: true,
            type:'string'
          },
        },{
          type: 'date',
          modelKey: 'date',
          label: '出生日期',
          props: {
            placeholder: '请输入日期'
          },
          rules: {
            required: true,
            type:'string',
          }
        },
        {
          type: 'input',
          modelKey: 'email',
          label: '邮箱',
          props: {
            placeholder: '请输入'
          },
          rules: {
            required: true,
            type:'email'
          },
        },{
          type: 'input',
          modelKey: 'tel',
          label: '手机号',
          props: {
            placeholder: '请输入'
          },
          rules: {
            required: true,
            type:'tel'
          },
        },{
          type: 'input',
          modelKey: 'age',
          label: '年龄',
          props: {
            placeholder: '请输入'
          },
          rules: {
            required: true,
            type:'number'
          },
        },
        {
          type: 'select',
          modelKey: 'selectValue',
          label: '性别',
          props: {
            placeholder: '请输入',
            options: [{ text: '男', value: 'aa'}, { text: '女', value: 'bb' }]
          },
          rules: {
            required: true
          }
        },
        {
          type: 'select',
          modelKey: 'isFlag',
          label: '是否毕业',
          props: {
            placeholder: '请输入',
            options: [{ text: '是', value: true}, { text: '否', value: false }]
          },
          rules: {
            required: true
          }
        }
      ]
    }
  },
  mounted(){
    let data=[{label:'家庭住址',value:'eeee1',field:'inputValue',type:'input',rules:'',require:''},
            {label:'性别',value:'男',field:'selectValue',type:'select'},
            {label:'是否毕业',value:false,field:'switchValue',type:'switch'},
            {label:'学历',value:'高中',field:'radioValue',type:'radio-group'}]
    let model={};
    data.forEach(function (value) {
        console.log(value);
        model[value.field]=value.value;
    });
    // this.model=model;
  },
  methods:{
    setInput(res){//Set input data
      for (let key of Object.keys(this.model)) {
        if(key==res.field){
          this.model[key]=res.data;
        }
      }
    }
  },
  components:{
    yInput
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



