 * @Last Modified time: 2019-06-21 14:20:24
 * @Last  
 */
<template>
  <div class="yInput">
    <cube-form :model="model"  @validate="validateHandler" :options="options"  :immediate-validate="false" @submit="submitHandler">
      <div :key="index" v-for="(item,index) in fields">
        <!-- 下拉类型 -->
        <cube-form-item :field="item" v-if="item.type=='select'">
            <cube-input @focus="selectClick(item)" placeholder= '请选择' v-model="model[item.modelKey].text" ></cube-input>
        </cube-form-item>
        <!-- 日期类型 -->
        <cube-form-item :field="item" v-else-if="item.type=='date'">
            <cube-input @focus="dateClick(item)" placeholder= '请输入日期' v-model="model[item.modelKey]" ></cube-input>
        </cube-form-item>
        <!-- 其它 -->
        <cube-form-item :field="item" v-else></cube-form-item>
      </div>
      <cube-form-group>
          <cube-button type="submit" :primary="true">保存</cube-button>
      </cube-form-group>
    </cube-form>
  </div>
</template>
<script>
export default {
  props:['model','fields','input'],
  data() {
    return {
      validity: {},
      valid: undefined,
      fieldName:'',//field Name
      options: {
        layout: '' // classic fresh
      }
    }
  },
  methods: {
    submitHandler(e) {
      console.log(this.model)
    },
    validateHandler(result) {
      this.validity = result.validity
      this.valid = result.valid
    },
    /*.......................Select funtion..................... */
    selectClick(item){
      this.fieldName=item.modelKey;
      console.log(item)
      // request data....
      this.showPicker(item.props.options);
    },
    showPicker(res) {
      this.picker = this.$createPicker({
        title: '',
        data: [res],
        onSelect: this.selectHandle,
        onCancel: this.cancelHandle
      })
      this.picker.show()
    },
    // Select &emit.....;
    selectHandle(selectedVal, selectedIndex, selectedText,) {
      let data={
        field:this.fieldName,
        data:{
          value:selectedVal.join(','),
          text:selectedText.join(',')
        }
      }
      this.inputProps(data);
    },
    async inputProps(data){
      await this.$emit("input",data);
      this.fieldName='';
    },
    /*.......................Date type doing function ..................... */
    dateClick(item){
      this.fieldName=item.modelKey;
      this.showDatePicker();
    },
    showDatePicker() {
      this.datePicker = this.$createDatePicker({
        title: '',
        min: new Date(2008, 7, 8),
        max: new Date(2025, 9, 20),
        value: new Date(),
        onSelect: this. dateHandle,
        onCancel: this.cancelHandle
      })
      this.datePicker.show()
    },
     dateHandle(date, selectedVal, selectedText) {
      let data={
        field:this.fieldName,
        data:selectedVal.join(',')
      }
      this.inputProps(data);
    },
    /*....................public............................*/
    cancelHandle() {
      // clear fieldName;
      console.log('clear fieldName')
    },
    /*.........over........*/
  }
}
</script>
<style scoped>
.yInput .cube-form-item {
  display: block;
  text-align: left;
}
</style>



