                  <div v-if="item.ZZSHM != 'D'  && item.ZZSHM != 'T' && item.FTYPE != 'D' && item.ZKJLX != 'TXT' && item.ZKJLX != 'BUT' && item.ZKJLX != 'STA'  && item.ZKJLX != 'MON'&&item.ZZSHM != 'B' && item.ZZSHM != 'Z' && item.ZZSHM != 'S' && item.ZKJLX != 'PSR'" >
