    methods: {
      showDatePicker(title,val,item) {
        // title
        this.fieldsName=item;
        this.datePicker = this.$createDatePicker({
          title: title,
          min: new Date(2008, 7, 8),
          max: new Date(2020, 9, 20),
          value: val? new Date(val) : new Date(),
          onSelect: this.selectHandle
        })
        this.datePicker.show()
      },
      selectHandle(date, selectedVal, selectedText) {
        let t_this=this;
        t_this.$set(t_this.fieldsName,'value',selectedVal.join('-'));
        this.fieldsName='';
      },
      format(date, fmt) { //author: meizz
        var o = {
          "M+": date.getMonth() + 1, //月份
          "d+": date.getDate(), //日
          "h+": date.getHours(), //小时
          "m+": date.getMinutes(), //分
          "s+": date.getSeconds(), //秒
          "q+": Math.floor((date.getMonth() + 3) / 3), //季度
          "S": date.getMilliseconds() //毫秒
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
          if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
      },
      sortInfo(list, key, type) {
        list.sort(function (a, b) {
          if (type == "asc") {
            return a[key] - b[key];
          } else {
            return b[key] - a[key];
          }
        })
        return list;
      },
      cloneObj(obj) {
        var str, newobj = obj.constructor === Array ? [] : {};
        if(typeof obj !== 'object'){
          return;
        } else if(window.JSON){
          str = JSON.stringify(obj), //序列化对象
            newobj = JSON.parse(str); //还原
        } else {
          for(var i in obj){
            newobj[i] = typeof obj[i] === 'object' ? cloneObj(obj[i]) : obj[i];
          }
        }
        return newobj;
      },
      handleClick(res) {//切换显示的面板
        this.activeName=res;
      },
      closeInfo(){
        this.activeName='';
      },
      getTreeNode(chooseObj, item) {
        item.value = chooseObj.id + "-" + chooseObj.value;
      },
      getPernrData(data,item) {
        item.value = {
          KEY: data.employeeNum,
          VALUE: data.employeeName + "(" + data.employeeNum + ")"
        };
      },
