          var group = [];  //分组
          var inf = [];    //信息类型
          t_this.zacti = "";
          if (data != "") {
            for (var i in data) {
              if (data[i].pname == "ET_FILE") {  //获取附件信息
                files = JSON.parse(data[i].value);
              } else if (data[i].pname == "ET_GRP") { //获取分组信息
