                var sonGroup = group[i].ACBLK[j].INFTY;
                for (var k in sonGroup) {
                  if (sonGroup[k].ZZTAB == "X") {  //显示的多数据 && sonGroup[k].ACTIO != "DIS"
                    var valueList = sonGroup[k].VALUE != "" && sonGroup[k].VALUE != undefined ? JSON.parse(sonGroup[k].VALUE) : [];
                    sonGroup[k].models = [];
