                        } else {
                          if (fields[o].ZZDEF != "") {
                            fields[o].value = fields[o].ZZDEF;
                            if (fields[o].ZZBDS != "") {
                              fields[o].ZZBDS = new RegExp(fields[o].ZZBDS.replace(/\s/g, "")); //处理正则表达式
                            } else {
                              fields[o].ZZBDS = "";
                            }
                          } else {
                            fields[o].value = "";
                            if (fields[o].ZZBDS != "") {
                              fields[o].ZZBDS = new RegExp(fields[o].ZZBDS.replace(/\s/g, "")); //处理正则表达式
                            } else {
                              fields[o].ZZBDS = "";
                            }
                          }

                        }
                      }
                    }
                  } else if (sonGroup[k].ZZTAB == "X" && (sonGroup[k].ACTIO == "DIS" || sonGroup[k].ACTIO == "MOD")) {  //显示的多数据 不需要加 添加删除按钮按钮
                    var valueList = sonGroup[k].VALUE != "" ? JSON.parse(sonGroup[k].VALUE) : "";
                    var models = sonGroup[k].models;
                    for (var o  in valueList) {
                      var values = valueList[o];
                      for (var l in models[o]) {
                        if (models[o][l].ZZREQ == "X") {
                          let rule = [{required: true}]
                          t_this.rules[models[o][l].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                        }
                        models[o][l].value = values[models[o][l].FIELD];
                        if (models[o][l].FTYPE == 'D') {   //日期类型转换格式
                          models[o][l].value = models[o][l].value != "" && models[o][l].value != "00000000" ? t_this.format(new Date(models[o][l].value.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")), "yyyy-MM-dd") : "";
                        } else if (models[o][l].ZZSHM == 'D') { //下拉列表转换格式
                          if (models[o][l].value != "") {
                            models[o][l].value = JSON.parse(values[models[o][l].FIELD]);
                            models[o][l].valueList = [];
                            models[o][l].valueList.push(models[o][l].value);
                          } else {
                            models[o][l].value = "";
                            models[o][l].valueList = [];
                          }
                        } else if (models[o][l].ZZSHM == 'T') { //树转换格式
                          models[o][l].isShow = false;
                          models[o][l].treeDataCreate = "";
                          if (models[o][l].value != "") {
                            var value = JSON.parse(values[models[o][l].FIELD]);
                            models[o][l].value = value.KEY != "" ? value.KEY + "-" + value.VALUE : value.VALUE;
                          } else {
                            models[o][l].value = "";
                          }

                        } else {
                          if (models[o][l].value != "") {
                            models[o][l].value = values[models[o][l].FIELD];
                            if (models[o][l].ZZBDS != "") {
                              models[o][l].ZZBDS = new RegExp(models[o][l].ZZBDS.replace(/\s/g, "")); //处理正则表达式
                            } else {
                              models[o][l].ZZBDS = "";
                            }
                          } else {
                            models[o][l].value = "";
                            if (models[o][l].ZZBDS != "") {
                              models[o][l].ZZBDS = new RegExp(models[o][l].ZZBDS.replace(/\s/g, "")); //处理正则表达式
                            } else {
                              models[o][l].ZZBDS = "";
                            }
                          }

                        }
                      }
                    }

                  } else {
                    var fields = t_this.sortInfo(sonGroup[k].FIELD, "SORTD", "asc");// this.cloneObj(obj.FIELD);sonGroup[k].FIELD;
                    for (var n in fields) {
                      if (fields[n].ZZREQ == "X") {
                        let rule = [{required: true}]
                        t_this.rules[fields[n].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                      }
                      if (fields[n].FTYPE == 'D') {   //日期类型转换格式
                        fields[n].value = fields[n].ZZDEF != "" && fields[n].ZZDEF != "00000000" ? t_this.format(new Date(fields[n].ZZDEF.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")), "yyyy-MM-dd") : "";
                      } else if (fields[n].ZZSHM == 'D') { //下拉列表转换格式
                        if (fields[n].ZZDEF != "") {
                          // debugger
                          var _data={
                            value:JSON.parse(fields[n].ZZDEF).KEY,
                            text:JSON.parse(fields[n].ZZDEF).VALUE
                          }
                          fields[n].value =_data;
