                          fields[n].value = value.KEY + "-" + value.VALUE;
                        } else {
                          fields[n].value = "";
                        }

                      } else {
                        if (fields[n].ZZDEF != "") {
                          fields[n].value = fields[n].ZZDEF;
                          if (fields[n].ZZBDS != "") {
                            fields[n].ZZBDS = new RegExp((fields[n].ZZBDS).replace(/\s/g, "")); //处理正则表达式
                          } else {
                            fields[n].ZZBDS = "";
                          }
                          fields[n].ZZBDS = ""; //处理正则表达式
                        } else {
                          fields[n].value = "";
                          if (fields[n].ZZBDS != "") {
                            fields[n].ZZBDS = new RegExp((fields[n].ZZBDS).replace(/\s/g, "")); //处理正则表达式
                          } else {
