          }
          t_this.groupList = group;
          /**
           * 区分附件 必填和非必填
           * @type {Array}
           */
          t_this.fileList = []; //清空附件
          t_this.fileListDefinition = []; // 清空附件自定义
          for (var i in files) {
            if (files[i].ZZREQ == "X") {
              t_this.fileList.push(files[i]);
            } else {
