        // });
      },
      /**
       * 修改
       * 加载屏幕字段和数据
       */
      getAcData(even) {
        let t_this = this;
        var params = {
          guid: t_this.GUID,
          even: "",
          userId:"",
          //bip: t_this.STAT == 'K' || t_this.STAT == 'Q' ? "X" : "",
          ipage: t_this.MENUII,
          ztype:t_this.ZTYPE
        }
        //http("get", url, params, {}, function (response) {
        httpRequest4Post(t_this, t_this.servicePath + "getAcData", params, function (response) {
          t_this.groupList =[];
          var data = response.data.result;
          var files = []; //附件
          var group = [];  //分组
          var fileData = [];
          t_this.rules = {};
          t_this.zacti = "";
          if (data != "") {
            for (i in data) {
              if (data[i].pname == "ET_GRP") {
                group = JSON.parse(data[i].value)  //获取分组信息
              } else if (data[i].pname == "EV_DATA") {
                fileData = JSON.parse(data[i].value)  //获取分组对应的数据VALUE

              } else if (data[i].pname == "EV_ZACTI") {
                t_this.zacti = JSON.parse(data[i].value)   //获取模板编码
              } else if (data[i].pname == "EV_PERNR") {
                if (JSON.parse(data[i].value) != "") {
                  t_this.employeeNum = JSON.parse(data[i].value);
                  t_this.getHeadInfo(JSON.parse(data[i].value));
                }

              } else if (data[i].pname == "ET_FILE") {
                files = JSON.parse(data[i].value);
              } else if (data[i].pname == "ES_MESSAGE") {
                var message = JSON.parse(data[i].value);
                if (message.MSGTY == "E") {  //戳无错误消息的提示
                  t_this.$message.error(message.MSGTX);
                  return;
                }
              }
            }

            //考虑到多个教育信息的
            for (var i in group) {
              for (var j in group[i].ACBLK) {
                var sonGroup = group[i].ACBLK[j].INFTY;
                for (var k in sonGroup) {
                  for (var s in fileData) {
                    if (sonGroup[k].INFTY == fileData[s].INFTY && sonGroup[k].SUBTY == fileData[s].SUBTY) {
                      var valueList = JSON.parse(fileData[s].VALUE);
                      sonGroup[k].models = [];
                      if (valueList.length > 1) {   //多value 多模板
                        for (var n in valueList) {
                          sonGroup[k].models.push(t_this.cloneObj(sonGroup[k].FIELD));
                        }
                      } else {
                        sonGroup[k].models.push(t_this.cloneObj(sonGroup[k].FIELD));
                      }
                    }
                  }

                }
              }
            }

            if (fileData == "") {
              return;
            }
            //考虑到字段有默认值
            for (var i in group) {
              group[i].ACBLK = t_this.sortInfo(group[i].ACBLK, "SORTD", "asc");
              for (var j in group[i].ACBLK) {
                var sonGroup = t_this.sortInfo(group[i].ACBLK[j].INFTY, "SORTD", "asc");
                for (var k in sonGroup) {
                  for (var t in fileData) {
                    if (fileData[t].INFTY == sonGroup[k].INFTY && fileData[t].SUBTY == sonGroup[k].SUBTY) { //value跟模板对应上的判断根据编码匹配
                      var valueList = JSON.parse(fileData[t].VALUE);
                      if (sonGroup[k].ZZTAB == "X") {
                        var models = sonGroup[k].models;
                        for (var o  in valueList) {
                          var values = valueList[o];
                          for (var l in models[o]) {
                            models[o][l].value = values[models[o][l].FIELD] == undefined ? "" : values[models[o][l].FIELD];
                            if (models[o][l].ZZREQ == "X") {
                              let rule = [{required: true}]
                              t_this.rules[models[o][l].FIELD + sonGroup[k].INFTY + sonGroup[k].SUBTY + group[i].ACGRP + group[i].ACBLK[j].ACBLK] = rule;
                            }
                            if (models[o][l].FTYPE == 'D') {   //日期类型转换格式
                              models[o][l].value = models[o][l].value != "" && models[o][l].value != "00000000" ?  t_this.$DateUtils.format(new Date(models[o][l].value.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")),"yyyy-MM-dd") : "";
                            } else if (models[o][l].ZZSHM == 'D') { //下拉列表转换格式
                              if (models[o][l].value != "") {
                                models[o][l].value = JSON.parse(values[models[o][l].FIELD]);
                                models[o][l].valueList = [];
                                models[o][l].valueList.push(models[o][l].value);
                              } else {
                                if (models[o][l].ZZDEF != "") {
                                  models[o][l].value = JSON.parse(models[o][l].ZZDEF);
                                  models[o][l].valueList = [];
                                  models[o][l].valueList.push(JSON.parse(models[o][l].ZZDEF));
                                } else {
                                  models[o][l].value = "";
                                  models[o][l].valueList = [];
                                }

                              }
                            } else if (models[o][l].ZZSHM == 'T') { //树转换格式
