      //页面初始化函数
      init(employeeNum){
        debugger
        let _this = this;
        //获取路由参数
        _this.info = this.$route.params.info;
        //_this.MENUII = sessionStorage.pageCode;
        if (_this.info) {
          let paramData =JSON.parse(_this.info);
          _this.MENUII = paramData.MENUII != undefined ? paramData.MENUII : "";//页面代码
          _this.SUBKEY = paramData.SUBKEY;//
          _this.func = paramData.FUNC;//功能编码
          _this.GUID = paramData.GUID != undefined  ? paramData.GUID : "";//guid
          _this.STAT = paramData.ZSTAT != undefined ? paramData.ZSTAT : "";//状态
          _this.ZTYPE = paramData.ZYWTYPE != undefined ? paramData.ZYWTYPE : "";//业务类型
        }
        if(_this.GUID != undefined && _this.GUID != "" && _this.employeeNum == ""){
          _this.getAcData();//修改
        }else if(employeeNum != undefined && employeeNum != ""){
          _this.initFileds(employeeNum)//新建
        }else{
          //转正打开页面需要判断是否有保存过的数据
          if(_this.MENUII == "PAZZGL" || _this.MENUII == "WYLZ" || _this.MENUII == "PADISP"){
            _this.employeeNum = _this.currentUser.PERNR;
            var param = {
              pernr:_this.employeeNum,
              ipage:_this.MENUII
            }
            httpRequest4Post(_this, _this.servicePath+"getGuid", param, function (response) {
              let data = response.data.result
              let length = data.length;
              for (let i = 0; i < length; i++) {
                if ('EV_GUID' === data[i].pname) {
                  _this.GUID = JSON.parse(data[i].value);
                  if(_this.GUID != "" && _this.GUID != "00000000000000000000"){
                    _this.getAcData();
                  }else{
                    _this.getHeadInfo(_this.employeeNum);//人员抬头信息
                    _this.initFileds(_this.employeeNum);//新建
                  }
                }
              }
            }, function (response) {
            });
          }else if(_this.MENUII == "PARZGL" || (_this.MENUII == "PALZGL" && _this.SUBKEY == "90") ){//不需要人员搜索框
            _this.isSearch = false;
            _this.initFileds("");//新建
          }else{
            _this.isSearch = true;
          }
        }
        //获取页面操作按钮
        // this.getEventOperatingButton()
      },
