      /**
       * 清空字段的value
       * @param fields  需要清空value字段集合
       * @param items 字段集合
       */
      emptyField(fields, items) {
        if (fields.length > 0) {
          if (items.FIELD == undefined) {
            for (var i in items) {
              for (var j in fields)
                if (items[i].FIELD == fields[j].FIELD) {
                  //如果是树value = ""
                  if (items[i].ZZSHM == 'T') {
                    items[i].value = "";
                    items[i].orgList = [];
                  } else {
                    items[i].value = {KEY: "", VALUE: ""};
                    items[i].valueList = [];
                  }

                }
            }
          } else {
            for (var i in items.FIELD) {
              for (var j in fields)
                if (items.FIELD[i].FIELD == fields[j].FIELD) {
                  //如果是树value = ""
                  if (items.FIELD[i].ZZSHM == 'T') {
                    items.FIELD[i].value = "";
                    items.FIELD[i].orgList = [];
                  } else {
                    items.FIELD[i].value = {KEY: "", VALUE: ""};
                    items.FIELD[i].valueList = [];
                  }

                }
            }
          }
        }
      },
      getDataSearch(obj) {
        let t_this = this;
        var iv_data = [];
        var obj2 = {};
        var fields = [];
        if (obj instanceof Array) {
          fields = obj;
        } else {
          fields = obj.FIELD;
        }

        for (var n in fields) {
          if (fields[n].FTYPE == 'D') {   //日期类型转换格式
            obj2[fields[n].FIELD] = fields[n].value != "" && fields[n].value != undefined ? t_this.format(new Date(fields[n].value), "yyyyMMdd") : "";
          } else if (fields[n].ZZSHM == 'D' || fields[n].ZZSHM == 'Z' || fields[n].ZZSHM == 'B' || fields[n].ZZSHM == 'S') { //下拉列表转换格式
            obj2[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? (fields[n].value.KEY == undefined ? "" : JSON.stringify(fields[n].value)) : "";
          } else if (fields[n].ZZSHM == 'T') { //树转换格式
            var value = fields[n].value;
            if (value != undefined && value != "") {
              var values = value.split("-");
              obj2[fields[n].FIELD] = JSON.stringify({
                KEY: value.split("-")[0],
                VALUE: value.split("-")[values.length - 1]
              });
            } else {
              obj2[fields[n].FIELD] = JSON.stringify({
                KEY: "",
                VALUE: ""
              });
            }
          }else if(fields[n].ZKJLX == "PSR"){
            obj2[fields[n].FIELD] = (fields[n].value != undefined && fields[n].value != "") ? fields[n].value : "";
          } else {
            obj2[fields[n].FIELD] = fields[n].value == undefined ? "" : fields[n].value;
          }
        }
        iv_data.push(obj2);
        return iv_data;
      },
      /**
       * 获取页面输入框的数据
       */
      getData() {
        let t_this = this;
        var group = t_this.groupList;
        var iv_data = [];
        for (var i in group) {
          var sonGroup = group[i].ACBLK;
          for (var j in sonGroup) {
            var inftyList = sonGroup[j].INFTY;
            for (var k in inftyList) {
              if (inftyList[k].ZZTAB == 'X') {
                var models = inftyList[k].models;
                var objectData = {
                  INFTY: inftyList[k].INFTY,
                  SUBTY: inftyList[k].SUBTY,
                  VALUE: []
                };
                for (var o in models) {
                  var obj = {};
                  var fields = models[o];
                  for (var n in fields) {
                    if (fields[n].FTYPE == 'D') {   //日期类型转换格式
                      obj[fields[n].FIELD] = fields[n].value != "" && fields[n].value != undefined ? t_this.format(new Date(fields[n].value), "yyyyMMdd") : "";
                    } else if (fields[n].ZZSHM == 'D' || fields[n].ZZSHM == 'Z' || fields[n].ZZSHM == 'B' || fields[n].ZZSHM == 'S') { //下拉列表转换格式
                      obj[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? (fields[n].value.KEY == undefined ? "" : JSON.stringify(fields[n].value)) : "";
                    } else if (fields[n].ZZSHM == 'T') { //树转换格式
                      var value = fields[n].value;
                      if (value != undefined && value != "") {
                        var values = value.split("-");
                        obj[fields[n].FIELD] = JSON.stringify({
                          KEY: value.split("-")[0],
                          VALUE: value.split("-")[values.length - 1]
                        });
                      } else {
                        obj[fields[n].FIELD] = JSON.stringify({
                          KEY: "",
                          VALUE: ""
                        });
                      }
                    }else if(fields[n].ZKJLX == "PSR"){
                      obj[fields[n].FIELD] = (fields[n].value != undefined && fields[n].value != "") ? JSON.stringify(fields[n].value) : "";
                    } else {
                      obj[fields[n].FIELD] = fields[n].value == undefined ? "" : fields[n].value;
                    }
                  }
                  objectData.VALUE.push(obj);

                }
                objectData.VALUE = JSON.stringify(objectData.VALUE); //装换数据
                iv_data.push(objectData);
              } else {
                var objectData = {
                  INFTY: inftyList[k].INFTY,
                  SUBTY: inftyList[k].SUBTY,
                  VALUE: []
                };
                var obj = {};
                var fields = inftyList[k].FIELD;
                for (var n in fields) {
                  if (fields[n].FTYPE == 'D') {   //日期类型转换格式
                    obj[fields[n].FIELD] = fields[n].value != "" && fields[n].value != undefined ? t_this.format(new Date(fields[n].value), "yyyyMMdd") : "";
                  } else if (fields[n].ZZSHM == 'D' || fields[n].ZZSHM == 'Z' || fields[n].ZZSHM == 'B' || fields[n].ZZSHM == 'S') { //下拉列表转换格式
                    obj[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? JSON.stringify(fields[n].value) : "";
                  } else if (fields[n].ZZSHM == 'T') { //树转换格式
                    var value = fields[n].value;
                    if (value != undefined && value != "") {
                      var values = value.split("-");
                      obj[fields[n].FIELD] = JSON.stringify({
                        KEY: value.split("-")[0],
                        VALUE: value.split("-")[values.length - 1]
                      });
                    } else {
                      obj[fields[n].FIELD] = JSON.stringify({
                        KEY: "",
                        VALUE: ""
                      });
                    }

                  }else if(fields[n].ZKJLX == "PSR"){
                    obj[fields[n].FIELD] = fields[n].value != undefined && fields[n].value != "" ? JSON.stringify(fields[n].value) : "";
                  } else {
                    obj[fields[n].FIELD] = fields[n].value == undefined ? "" : fields[n].value;
                  }
                }
                objectData.VALUE.push(obj);
                objectData.VALUE = JSON.stringify(objectData.VALUE); //装换数据
                iv_data.push(objectData);
              }


            }
          }
        }
        return iv_data;
      },
      /**
       * 数据操作核心URL
       * @param stats
       * @param type  save ：保存数据  submit： 提交  confirm:确认
       */
      allSubmit(objParam) {
        debugger;
        let t_this = this;
        console.log(t_this.getData());

        //页面input输入框的数据是否正确
        if(t_this.errCount > 0){
          return;
        }
        var files = t_this.fileList.concat(t_this.fileListDefinition);
        //var base64 = getPhoto(files);
        /*if(!base64){
          this.$message.error("一寸头像照片格式不对，应该为.png,.jpg");
          return;
        }*/

        var paEventVo = {
          userId:"",
          pernr: t_this.employeeNum,  //查询员工编号
          zacti: t_this.zacti,    // 事件编号
          guid: t_this.GUID,
          stats: t_this.getStats(objParam),//状态
          ipage: t_this.MENUII,
          data: JSON.stringify(t_this.getData()), //是级联数据 否则 为空
          url: "",
          check: "",//"submit" == type || "confirm" == type ? "X" : "",
          fileList: JSON.stringify(files),
          subkey: t_this.SUBKEY,
          func: t_this.func,
          photo:"",
          ztype:t_this.ZTYPE
        }
        return;
        t_this.loading =true;
        httpRequest4Post(t_this, t_this.servicePath + "saveData", paEventVo, function (response) {
          t_this.loading =false;
          var dataList = response.data.result;
          var message = "";
          for (var i in dataList) {
            if (dataList[i].pname == "ES_MESSAGE") {
              var message = JSON.parse(dataList[i].value);
              if (message.MSGTY == "E") {  //戳无错误消息的提示
                t_this.$alert(message.MSGTX, '提示', {type: 'warning', center: true});
                return;
              }
            } else if (dataList[i].pname == "EV_GUID") {
              t_this.GUID = JSON.parse(dataList[i].value);
              //发送通知
              if(t_this.MENUII == "PARZGL" && objParam.FCODE == "RETURN" || objParam.FCODE == "SEND"){
                t_this.dialogTitle = "发送消息";
                t_this.sendMessageData = {"GUID":t_this.GUID,"MENUII":t_this.MENUII,"STATS":objParam.FTATS};
                t_this.dialogVisible = true;
              }else{
                t_this.$confirm(message.MSGTX,'提示',{
                  confirmButtonText:'确定',
                  cancelButtonText:'取消',
                  center:true
                }).then(()=>{
                 if(objParam.FCODE != "SAVE"){
                   t_this.returnBack();
                 }
                }).catch(()=>{
                  //取消
                })
                t_this.getAcData(); //保存之后读取数据
              }
              return;
            }
          }
        });
      },
      //获取提交的状态
      getStats(item){
        let stats = item.FTATS;//默认传button执行状态
        if(this.MENUII == "PARZGL" && this.FCODE == "SEND"){
          stats = item.STATS;
        }
        return stats;
      },
      /**
       * 获取个信息的 抬头信息
       * @employeeNum {string}
       */
      getHeadInfo(employeeNum) {
        let t_this = this;
        var params = {
          userId:"",
          pernr: employeeNum,
          ipage: t_this.MENUII,  //二级菜单编码
          date: t_this.format(new Date(), "yyyyMMdd")
        }
        httpRequest4Post(t_this, t_this.servicePath + "getHeadInfo", params, function (response) {
          var data = response.data.result;
          if (data != "") {
            for (var i in data) {
              if (data[i].pname == "ET_DATA") {
                t_this.headhtmlData = JSON.parse(data[i].value);
              } else if (data[i].pname == "EV_PHOTO") {
                if (JSON.parse(response.data[i].value) != "") {
                  t_this.photo = "data:image/jpg;base64," + JSON.parse(response.data[i].value);
                } else {
                  t_this.photo = "";
                }
              }
            }
          }
        });
      },

      /**
       *根据字段value的改变带出相关字段信息，字段联动
       * @param event
       * @param value
       * @param field
       * @param infty
       * @param subty
       * @param ACGRP
       * @param ACBLK
       * @param item
       */
      change(event, value, field, infty, subty, ACGRP, ACBLK, item){
        console.log('change')
        if (event != "X") {
          return;
        }
        var defValue = "";
        if (item.ZZSHM == 'T') {
          defValue = JSON.stringify({KEY: value.split("-")[0], VALUE: value.split("-")[2]})
        } else if (item.ZZSHM == "D") {
          defValue = JSON.stringify(value)
        } else if (item.FTYPE == 'D') {
          defValue = this.format(value,"yyyyMMdd")
        } else {
          defValue = value;
        }
        let param = {
          pernr: this.employeeNum,
          zacti: this.zacti,
          guid: this.GUID,
          data: JSON.stringify(this.getData()),
          isField: JSON.stringify({
            ACGRP: ACGRP,
            ACBLK: ACBLK,
            INFTY: infty,
            SUBTY: subty,
            FIELD: field,
            ZZDEF: defValue
          })
        }
        let _this = this;
        httpRequest4Post(_this, _this.servicePath+"getCascadeInfo",param,function(response) {
          var data = response.data.result;
          if (data != "") {
            var jsonList = "";
            var msg = "";
            for (var i in data) {
              if (data[i].pname == "ET_FIELD") {
                jsonList = JSON.parse(data[i].value);
              } else if (data[i].pname == "ES_MESSAGE") {
                msg = JSON.parse(data[i].value);
              }
            }
            if (msg != "") {
              if (msg.MSGTY == "E") {
                _this.$message.error(msg.MSGTX)
                item.value = "";
                return;
              } else if (msg.MSGTY == "W") {
                this.$confirm(msg.MSGTX,{
                  confirmButtonText:'确定',
                  cancelButtonText:'取消',
                  center:true
                }).then(()=>{
                  var groupList = _this.groupList;
                  for (var i in jsonList) {
                    for (var j in groupList) {
                      if (groupList[j].ACGRP == jsonList[i].ACGRP) {
                        var sonGroup = groupList[j].ACBLK;
                        for (var k in sonGroup) {
                          if (sonGroup[k].ACBLK == jsonList[i].ACBLK) {
                            var inftyList = sonGroup[k].INFTY;
                            for (var o in inftyList) {
                              if (inftyList[o].INFTY == jsonList[i].INFTY && inftyList[o].SUBTY == jsonList[i].SUBTY) {
                                var fieldList = inftyList[o].FIELD;
                                for (var n in fieldList) {
                                  if (fieldList[n].FIELD == jsonList[i].FIELD) {
                                    if (fieldList[n].ZZSHM == "T") {
                                      if (jsonList[i].ZZDEF == "") {
                                        fieldList[n].value = "";
                                        fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                        fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                        fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                      } else {
                                        var obj = JSON.parse(jsonList[i].ZZDEF);
                                        fieldList[n].value = obj.KEY + "-" + obj.VALUE;
                                        fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                        fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                        fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                      }
                                    } else if (fieldList[n].ZZSHM == "D") {
                                      if (jsonList[i].ZZDEF == "") {
                                        fieldList[n].value = "";
                                        fieldList[n].valueList = [];
                                        fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                        fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                        fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                      } else {
                                        var obj = JSON.parse(jsonList[i].ZZDEF);
                                        fieldList[n].value = obj;
                                        fieldList[n].valueList =[];//清空原有选项
                                        fieldList[n].valueList.push(JSON.parse(jsonList[i].ZZDEF));
                                        fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                        fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                        fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                      }

                                    } else if (fieldList[n].FTYPE == "D") {
                                      fieldList[n].value = jsonList[i].ZZDEF != "" && jsonList[i].ZZDEF != "00000000" ? new Date(jsonList[i].ZZDEF.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")) : "";
                                      fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                      fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                      fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                    } else {
                                      fieldList[n].value = jsonList[i].ZZDEF;
                                      fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                      fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                      fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                      fieldList[n].ZZBDS = new RegExp((jsonList[i].ZZBDS).replace(/\s/g, "")); //处理正则表达式
                                    }
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }).catch(()=>{
                  item.value = "";
                  return;
                })

              } else {
                var groupList = _this.groupList;
                for (var i in jsonList) {
                  for (var j in groupList) {
                    if (groupList[j].ACGRP == jsonList[i].ACGRP) {
                      var sonGroup = groupList[j].ACBLK;
                      for (var k in sonGroup) {
                        if (sonGroup[k].ACBLK == jsonList[i].ACBLK) {
                          var inftyList = sonGroup[k].INFTY;
                          for (var o in inftyList) {
                            if (inftyList[o].INFTY == jsonList[i].INFTY && inftyList[o].SUBTY == jsonList[i].SUBTY) {
                              var fieldList = inftyList[o].FIELD;
                              for (var n in fieldList) {
                                if (fieldList[n].FIELD == jsonList[i].FIELD) {
                                  if (fieldList[n].ZZSHM == "T") {
                                    if (jsonList[i].ZZDEF == "") {
                                      fieldList[n].value = "";
                                      fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                      fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                      fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏

                                    } else {
                                      var obj = JSON.parse(jsonList[i].ZZDEF);
                                      fieldList[n].value = obj.KEY + "-" + obj.VALUE;
                                      fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                      fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                      fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏

                                    }
                                  } else if (fieldList[n].ZZSHM == "D") {
                                    if (jsonList[i].ZZDEF == "") {
                                      fieldList[n].value = "";
                                      fieldList[n].valueList = [];
                                      fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                      fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                      fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                    } else {
                                      var obj = JSON.parse(jsonList[i].ZZDEF);
                                      fieldList[n].value = obj;
                                      fieldList[n].valueList =[];//清空原有选项
                                      fieldList[n].valueList.push(JSON.parse(jsonList[i].ZZDEF));
                                      fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                      fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                      fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                    }

                                  } else if (fieldList[n].FTYPE == "D") {
                                    fieldList[n].value = jsonList[i].ZZDEF != "" && jsonList[i].ZZDEF != "00000000" ? new Date(jsonList[i].ZZDEF.replace(/^(\d{4})(\d{2})(\d{2})$/, "$1-$2-$3")) : "";
                                    fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                    fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                    fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                  } else {
                                    fieldList[n].value = jsonList[i].ZZDEF;
                                    fieldList[n].ZZDIS = jsonList[i].ZZDIS; //是否可输入
                                    fieldList[n].ZZREQ = jsonList[i].ZZREQ; //是否必填
                                    fieldList[n].ZZHID = jsonList[i].ZZHID; //是否隐藏
                                    fieldList[n].ZZBDS = new RegExp((jsonList[i].ZZBDS).replace(/\s/g, "")); //处理正则表达式
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        },function(response) {
          _this.$message.error('异常错误,请联系管理员!');
        });

      },
      /**
       * 添加模板
       * @param item
       * @param index
       */
      addModel(item,index){
        var obj = item;
        var models = obj.models;
        var model = this.sortInfo(this.cloneObj(obj.FIELD), "SORTD", "asc");// this.cloneObj(obj.FIELD);
        for(var i in model){
          if(model[i].ZZBDS != "" && typeof(model[0].ZZBDS) != "object"){
            model[i].ZZBDS = new RegExp(model[i].ZZBDS.replace(/\s/g, ""))
          }
        }
        models.splice(index + 1 ,0 ,model);
      },
      /**
       * 删除模板
       * @param item
       * @param index
       */
      deleteModel(item,index){
        var obj = item;
        var models = obj.models;
        if(models.length > 1){
          this.del(models,index);
        }else{
          this.$message.warning("已经是最后一个");
        }
      },
      del(list,index){
        if(isNaN(index) || list.length < 1){
          return false;
        }else{
          list.splice(index,1);
        }
      },
      displayFile(){
        if (this.fileList.length > 0 || this.fileListDefinition.length > 0) {
          this.isFile = true;
        }
      },
      /**
       * 附件上传成功回调函数
       * @param response
       * @param file
       * @param fileList
       * @param myItem
       */
      handleSuccess(response,file,fileList,myItem){
        myItem.ATTNN = file.name;//文件名称
        myItem.ATTDL = myItem.ZATTT != "01" ? file.response :  file.response.qingUrl;//文件下载
        myItem.ATTUP =  myItem.ZATTT != "01" ? file.response :  file.response.qingUrl;//文件地址
        if(myItem.ZATTT == "01"){
          //图像识别
          this.orcImgRecognition(file.response.localUrl);
        }
      },
      /**
       * 删除附件
       * @param item
       */
      deleteUpload(item){
        item.ATTNN = "";//文件名称
        item.ATTDL = "";//文件地址
        item.ATTUP = "";//文件下载地址
      },
      /**
       * 添加自定义附件
       */
      addFile(file){
        var file = {
          "ZACTI": "91",
          "ZATTT": "",
          "ZATTN": "",
          "ZZREQ": "",
          "ZTYPE": "U",
          "SORTID": "1000",
          "ATTNN": "",
          "ATTUP": "",
          "ATTDL": "",
          "ZDIS":"",
          "NEWFILE":true
        }
        this.fileListDefinition.push(file);
        //this.uploadItems1.push({title:'',fileList: [], fileName:'',new:'1'})
      },
      /**
       * 删除自定义附件
       * @param index
       */
      removeDiv(index){
        this.fileListDefinition.splice(index,1)
      },
      downloadFile(file){
        if(file.ATTNN.substr(file.ATTNN.lastIndexOf(".")).toLocaleLowerCase() == ".pdf"){
          let url =  encodeURI("/fileService/qingStor/downloadFile?url=" + encodeURIComponent(file.ATTDL)+"&fileName="+file.ATTNN);
          window.open('/static/pdf/web/viewer.html?file=' + encodeURIComponent(url));
        }else{
          window.open(encodeURI("/fileService/qingStor/downloadFile?url=" + encodeURIComponent(file.ATTDL)+"&fileName="+file.ATTNN));
        }
      },
      /**
       * 获取页面操作按钮
       */
      getEventOperatingButton(){
        let _this = this;
        var params = {
          userId:"",
          menuCode:_this.MENUII,
          subkey:_this.func,
          stats:_this.STAT
        };
        httpRequest4Post(_this, _this.servicePath+"getEventOperatingButton", params, function(response) {
          _this.buttonList = getResultValueByKey(response.data.result,"ET_FUNCODES");
        },function (response) {});
      },
      /**
       * g公共事件附件下载模板
       */
      downloadTemplate(item){
        if(this.MENUII == "PARZGL" ){
          //todo
          return;
        }
      },
      //图像识别
      orcImgRecognition(url){
        let _this = this;
        let params = {
          modelCode:"ID001",
          verifyImage: url,
          param:"F1"
        };
        _this.loading = true;
        httpRequest4Post(_this, _this.servicePath+"callOCRRecognition",params,function(response) {
          _this.loading = false;
          _this.emailForm.subject = getResultValueByKey(response.data.result,"EV_IMGDAT");
        },function(response) {
          _this.loading = false;
        });
      },
      /**
       * 获取个人信息
       * @param employee
       */
      getEmployeeDetail(data) {
        this.init( data.employeeNum);//调用初始化函数
        this.getHeadInfo(data.employeeNum);//人员抬头信息
        this.employeeNum = data.employeeNum;
      },
      //去除空格
      replaceAllSpace(val){
        if(typeof(val) == "string"){
          val = val.replace(/\s/g, "")
        }
        return val;
      },
      /**
       * 校验input框输入的数据
       * @param item
       */
      dataValidation (item){
        if(item.ZZBDS != "" && item.value != ""){
          if(!item.ZZBDS.test(item.value)){
            if(!item.isError){
              this.errCount+=1;
            }
            this.$set(item,'isError',true);
          }else{
            if(item.isError){
              this.errCount-=1;
            }
            this.$set(item,'isError',false)
          }
        }else {
          if(item.isError){
            this.errCount-=1;
          }
          this.$set(item,'isError',false)
        }
      },
      
    },
    created: function () {
      this._initData=_DATA.result;
      this.initFileds();
      //获取页面操作按钮
        // this.getEventOperatingButton()
    },
    watch:{
      '$route':function () {
        this.init();
      }
    }
  };
</script>
<style scoped>
 .titlelist {
   font-size: 20px;
  line-height: 60px;
 }
 
  .slide-fade-enter-active {
    transition: all .2s ease;
  }
  .slide-fade-leave-active {

    transition: all .2s ease;
  }
  .slide-fade-enter, .slide-fade-leave-to
  {
    left:0;right: 0;
    transform: translateX(50%);
    opacity: 0;
  }
  
  .info-content-b {
    margin-top: 56px;
    padding: 6px;
  }
  .info-title {
    height: 50px;
    background: #1089e0;
    position: fixed;
    width: 100%;
    z-index: 999;
    font-size: 14px;
    color: #fff;
    text-align: center;
    line-height:  50px;
  }
  input:focus {
    border: none
  }
  .info-title-left {
    position: absolute;
    left: 10px;
  }
  .info-title-right {
    position: absolute;
    right:10px;
  }
  .info-content {
    background:#fff;position:fixed;height:100%;width:100%;top:0px;bottom:0px;overflow: auto;
  }

  .main-content-title {
    font-size: 16px;
    border-left: 4px solid orange;
    padding-left: 5px;
    margin-top: 6px;
  }
  .main-field {
    font-size: 14px;
    line-height: 20px;
  }
</style>



